import { createWebHistory, createRouter } from "vue-router";
import Home from "./components/Home.vue"
import Argo from "./components/Argo.vue"
import Muko from "./components/Muko.vue"
import Xsaver from "./components/Xsaver.vue"
import About from "./components/About.vue"

const routes = [
  {
    path: "/portfolio",
    component: Home,
  },
  {
    path:"/about",
    component: About,
  },
  {
      path:"/argo",
      component: Argo,
  },
  {
    path:"/muko",
    component: Muko,
  },{
  path:"/xsaver",
  component: Xsaver,
}
];

const router = createRouter({
  history: createWebHistory(),
  routes,
});

export default router;``